// *** begin screen *** //
.screen {
    position: relative;
    display: flex;
    height: 98vh;
    padding-top: 140px;
    color: #fff;
    @include rmin (1921) {
        height: 100vh; }
    @include sm {
        height: 100vh; }
    @include xs {
        height: 100vh;
        padding-top: 130px; }
    @include mob-fix {
        height: 170vh; }
    &__decor {
        top: 0;
        left: 0;
        width: 100%;
        &_1 {
            top: -28px;
            height: 100%;
            z-index: -1;
            filter: drop-shadow(0 10px 30px rgba(0, 0, 0, 0.2));
            object-fit: cover;
            font-family: 'object-fit: cover;';
            @include xs {
                height: 120%; }
            .is-ie & {
                height: 112%; } }
        &_2 {
            z-index: -2;
            @include sm {
                top: 50%; }
            @include xs {
                top: 35%;
                height: 100%; } } }
    &__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 73.5%;
        @include xs {
            height: 100%; } }
    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 1006px;
        padding-left: 25px;
        @include sm {
            padding-left: 0; }
        @include xs {
            flex-direction: column; } }
    &__decor-triangle {
        position: absolute;
        top: -60%;
        left: -13%;
        z-index: -1;
        width: 452px;
        height: 434px;
        fill: #7352c5;
        @include sm {
            top: -50%;
            left: -15%;
            width: 412px;
            height: 374px; }
        @include xs {
            height: 260px;
            left: 50%;
            transform: translateX(-50%);
            width: auto; } }
    &__titles-wrap {
        position: relative;
        flex: 0 1 400px;
        max-width: 400px;
        padding-right: 90px;
        text-transform: uppercase;
        @include sm {
            max-width: none;
            flex: 0 1 370px; }
        @include xs {
            flex: 0 1 auto;
            margin-bottom: 30px;
            padding: 0 0 5px; }
        &::after {
            position: absolute;
            top: 58px;
            right: 0;
            width: 73px;
            height: 6px;
            background-color: $color5;
            content: "";
            @include sm {
                right: 20px; }
            @include xs {
                height: 3px;
                width: 108px;
                top: 100%;
                right: 12px; } } }
    &__title {
        margin-bottom: 20px;
        @include f('gp700', 150, 150);
        @include sm {
            font-size: 120px;
            line-height: 120px; }
        @include xs {
            font-size: 80px;
            line-height: 60px; } }
    &__sub-title {
        font-size: 60px;
        text-align: right;
        letter-spacing: 12px;
        @include sm {
            font-size: 44px; }
        @include xs {
            font-size: 28px; } }
    &__descr {
        flex: 0 1 calc(100% - 400px);
        padding-left: 35px;
        @include md {
            padding-left: 23px;
            flex: 0 1 calc(100% - 370px);
            max-width: none; }
        @include xs {
            flex: 0 1 auto;
            padding-left: 0; }
        .is-ie & {
            max-width: 588px; }
        &-text {
            margin-bottom: 31px;
            font-size: 26px;
            line-height: 40px;
            @include sm {
                font-size: 20px;
                line-height: 30px; }
            @include xs {
                margin-bottom: 20px;
                font-size: 15px;
                line-height: 22px; }
            &-mark {
                @include f('gp500');
                &_color1 {
                    color: #fe77af; }
                &_color2 {
                    color: #ffbd6a; } }
            &-btn {} } } }
//end screen //
//***begin about*** //
.about {
    margin: -100px 0 0;
    padding: 50px 0 15px;
    background: no-repeat 38% 36%;
    background-size: auto;
    @include sm {
        margin: 0; }
    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include xs {
            flex-direction: column; } }
    &__text {
        flex: 0 1 percentage(680 / 1140);
        margin-top: -30px;
        @include xs {
            flex: 0 1 auto;
            width: 100%;
            margin: 0 0 20px; } }
    &__title {
        max-width: 600px;
        margin-bottom: 45px; }
    &__descr {
        p {
            &:not(:last-child) {
                margin-bottom: 20px; } } }
    &__img {
        flex: 0 1 percentage(496 / 1140);
        margin-right: -25px;
        @include ratio(496,545);
        @include cover;
        @include xs {
            width: 100%;
            margin-right: 0; } } }
// end about //
//**begin services**//
.services {
    position: relative;
    padding: 23px 0 90px;
    @include xs {
        padding: 23px 0 40px; }
    &__decor {
        top: -26px;
        left: -30px;
        height: 258%;
        width: 26.7%; }
    &__title {
        max-width: 600px;
        margin: 0 auto 50px; }
    &__row {
        position: relative;
        margin: 0 -17px;
        @include md {
            justify-content: center; }
        @include xs {
            flex-direction: column;
            align-items: center;
            margin: 0 -15px; } }
    &__item {
        $parent: &;
        position: relative;
        flex: 0 1 calc(33.33333% - 34px);
        width: calc(33.33333% - 34px);
        max-width: 360px;
        height: 200px;
        margin: 0 17px;
        padding: 20px;
        cursor: pointer;
        @include md {
            flex: 0 1 calc(50% - 34px);
            width: auto; }
        @include xs {
            flex: 0 1 auto;
            width: calc(100% - 30px);
            margin: 0 15px;
            padding: 15px; }
        &_full {
            flex: 0 1 auto;
            width: 100%;
            height: 445px;
            max-width: none;
            margin: 0!important;
            cursor: default;
            @include xs {
                height: auto; }
            &:hover {
                transform: none; } }
        &.slick-slide {
            pointer-events: none; }
        &::after {
            position: absolute;
            top: 20px;
            left: 20px;
            right: 20px;
            bottom: 20px;
            border-radius: 4px;
            border: 1px solid $color3;
            opacity: 0.1;
            pointer-events: none;
            content: ''; }
        &:nth-child(1) ~ & {
            @include xs {
                margin-top: 30px; } }
        &:nth-child(2) ~ & {
            @include md {
                margin-top: 34px; } }
        &:nth-child(3) ~ & {
            @include md {
                margin-top: 34px; } }
        &-col {
            flex: 0 0 220px;
            text-align: center;
            @include md {
                flex: 0 1 160px; }
            @include xs {
                flex: - 1 auto;
                width: 100%;
                margin: 0 0 20px; } }
        &-info {
            flex: 0 1 calc( 100% - 220px );
            padding-left: 35px;
            @include md {
                flex: 0 1 calc(100% - 160px); }
            @include xs {
                flex: 0 1 auto;
                width: 100%;
                padding-left: 0; }
            &-text {
                height: 300px;
                font-size: 18px;
                line-height: 28px;
                @include md {
                    font-size: 16px;
                    line-height: 26px; }
                p {
                    &:not(:last-child) {
                        margin-bottom: 18px;
                        @include md {
                            margin-bottom: 12px; } } }
                &-inner {
                    padding: 15px 25px 0 0;
                    @include md {
                        padding: 15px 0 0; }
                    @include xs {
                        padding: 0; } }
                .mCSB_scrollTools {
                    right: -7px; } } }
        &-corner {
            position: absolute;
            width: 20px;
            height: 20px;
            border-style: solid;
            border-color: $color3;
            transition: .3s ease-in-out;
            #{$parent}:hover & {
                opacity: 0; }
            &_top {
                top: 20px;
                left: 20px;
                border-left-width: 1px;
                border-top-width: 1px;
                @include xs {
                    top: 15px;
                    left: 15px; }
                #{$parent}:hover & {
                    top: 0;
                    left: 0; } }
            &_bot {
                bottom: 20px;
                right: 20px;
                border-right-width: 1px;
                border-bottom-width: 1px;
                @include xs {
                    bottom: 15px;
                    right: 15px; }
                #{$parent}:hover & {
                    bottom: 0;
                    right: 0; } } }
        &-inner {
            display: flex;
            align-items: center;
            height: 100%;
            padding: 15px;
            @include xs {
                padding: 10px;
                justify-content: center; }
            #{$parent}_full & {
                padding: 0 0 16px 15px;
                align-items: flex-start;
                @include xs {
                    flex-direction: column;
                    padding: 5px; } } }
        &-icon {
            z-index: 2;
            &-wrap {
                position: relative;
                flex: 0 0 112px;
                height: 80px;
                @include flexCenter;
                @include xs {
                    transform: scale(.7); }
                &::after, &::before {
                    position: absolute;
                    z-index: 1;
                    width: 59px;
                    height: 59px;
                    background-color: #c2c2c2;
                    opacity: 0.1;
                    content: '';
                    border-radius: 50%; }
                &::after {
                    top: 40px;
                    left: 0; }
                &::before {
                    bottom: 42px;
                    right: 5px; }
                #{$parent}_full & {
                    width: 122px;
                    margin: 78px auto 53px; } } }
        &-text {
            padding-left: 20px;
            margin-top: 5px;
            @include xs {
                padding-left: 5px;
                flex: 1 1 150px; }
            #{$parent}_full & {
                padding-left: 0; }
            &_has-list {
                padding-left: 10px;
                @include xs {
                    padding-left: 5px;
                    flex-shrink: 0;
                    flex-grow: 1; } } }
        &-title {
            margin-bottom: 10px;
            @include f('gp700',22);
            color: $color4;
            @include xs {
                font-size: 18px;
                line-height: 20px; }
            #{$parent}-text_has-list & {
                padding-left: 10px; }
            #{$parent}_full & {
                margin-bottom: 15px; }
            span {
                font-size: 18px;
                color: $color2; } }
        &-price {
            span {
                @include f('gp700',18);
                color: $color2; } }
        &-list {
            font-size: 14px;
            @include xs {
                font-size: 13px; } }
        &-actions {
            margin-top: 20px;
            @include xs {
                display: flex;
                flex-direction: column;
                align-items: center; }
            &-btn {
                margin-right: 56px;
                padding: 14px 40px 13px;
                @include xs {
                    margin: 0 0 40px;
                    padding: 14px 15px 13px; } } } }
    &__content {
        position: absolute;
        top: -10px;
        bottom: 0;
        left: 17px;
        right: 17px;
        z-index: 10;
        height: 445px;
        opacity: 0;
        transform: scale(.8);
        transition: .3s ease-in-out;
        box-shadow: 0 15px 40px rgba(18, 3, 58, 0.15);
        border-radius: 4px;
        pointer-events: none;
        @include md {
            left: 6%;
            right: 6%;
            top: 50%;
            margin-top: -222px;
            bottom: auto; }
        @include xs {
           height: auto;
           margin-top: 0;
           top: 0;
           left: 15px;
           right: 15px; }
        &.is-active {
            pointer-events: auto;
            transform: scale(1);
            opacity: 1;
            .services__item-inner {
                pointer-events: auto; } }
        &-close {
            @include f('gp400');
            font-size: 16px;
            line-height: 28px;
            color: $color3;
            transition: .3s ease-in-out;
            &:hover {
                color: $color6; } } }
    &__slider {
        .slider-btn {
            margin-top: -23px;
            @include xs {
                margin-top: 0;
                top: calc(100% - 70px); }
            &_prev {
                left: -65px;
                @include md {
                    left: -40px; }
                @include xs {
                    left: 12%; } }
            &_next {
                right: -65px;
                @include md {
                    right: -40px; }
                @include xs {
                    right: 12%; } } } } }
// end services //
// ***begin how-to *** //
.how-to {
    padding: 70px 0;
    @include md {
        padding: 70px; }
    @include xs {
        padding: 30px 0 36px; }
    &__title {
        max-width: 500px;
        margin: 0 auto 47px; }
    &__row {
        margin: 0 -17px;
        justify-content: center;
        counter-reset: howToItem; }
    &__item {
        position: relative;
        flex: 0 1 calc(25% - 34px);
        height: 214px;
        max-width: 259.5px;
        min-width: 259.5px;
        margin: 0 17px;
        padding: 35px 25px 25px;
        text-align: center;
        counter-increment: howToItem;
        overflow: hidden;
        @include md {
            margin: 0 17px 34px; }
        &::before {
            position: absolute;
            left: -12px;
            top: 50%;
            transform: translateY(-50%);
            margin-top: 2px;
            text-shadow: 0 15px 40px rgba(18, 3, 58, 0.15);
            color: $color2;
            @include f('gp900');
            font-size: 34px;
            line-height: 18px;
            content: counter(howToItem,decimal-leading-zero); }
        &::after {
            position: absolute;
            top: 27px;
            left: -113px;
            opacity: 0.04;
            color: $color2;
            @include f('gp900');
            font-size: 305px;
            line-height: 200px;
            content: counter(howToItem,decimal-leading-zero);
            pointer-events: none; }
        &-icon {
            &-wrap {
                position: relative;
                z-index: 1;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                width: 90px;
                height: 90px;
                margin-bottom: 15px;
                border-radius: 50%;
                &_color-1 {
                    background-color: $color5; }
                &_color-2 {
                    background-color: #f1ae2f; }
                &_color-3 {
                    background-color: #fe77af; } } } } }
// end how to //

// *** begin train *** //
.train {
    position: relative;
    padding: 88px 0;
    @include xs {
        padding: 30px 0; }
    &__decor {
        right: 0;
        top: 2%;
        height: 123%;
        @include xs {
            top: 50%;
            height: 123%; } }
    &__container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @include xs {
            flex-direction: column; } }
    &__img {
        flex: 0 1 percentage(434 / 1140);
        margin: 10px 0 0 -25px;
        @include ratio(434,433);
        @include cover;
        @include xs {
            flex: 0 1 auto;
            width: 100%;
            margin: 0; } }
    &__text {
        flex: 0 1 percentage(672 / 1140);
        @include xs {
            flex: 0 1 auto; }
        p {
            font-size: 18px;
            line-height: 28px;
            &:not(:last-child) {
                margin-bottom: 20px; }
            span {
                @include f('gp700');
                color: $color5; } }
        &-inner {
            margin-bottom: 43px; }
        &-list {
             margin-bottom: 20px;
             li {
                &::before {
                     width: 6px;
                     height: 6px;
                     margin: 0 11px 0 6px;
                     background-color: #fe77af; }
                &::not(:last-child) {
                    margin-bottom: 5px; }
                span {
                    @include f('gp700');
                    color: $color2; } } } }
    &__title {
        margin-bottom: 44px; } }
// end train //

























