.contact-block {
    $parent: &;
    display: flex;
    align-items: center;
    line-height: 1;
    &_header {
        padding: 0 10px 0 20px;
        justify-content: space-between;
        @include sm {
            padding: 0; } }
    &_footer {
        flex: 0 1 766px;
        justify-content: space-between;
        @include xs {
            flex-direction: columns;
            flex: 0 1 auto;
            width: 100%; } }
    &__link {
        display: flex;
        align-items: inherit;
        font-size: 14px;
        color: $color3;
        transition: .3s ease-in-out;
        @include sm {
            font-size: 12px; }
        &:hover {
            color: $color5;
            svg {
                fill: $color5; } }
        #{$parent}_header & {
            &:nth-child(4) {
                display: none; } }

        #{$parent}_footer & {
            color: $white;
            &:hover {
                color: $color5; }
            &:not(:last-child) {
                @include xs {
                    margin-bottom: 15px; } } }
        svg {
            margin-right: 9px;
            fill: currentColor;
            font-size: 11px;
            transition: .3s ease-in-out; } }
    &__social {
        &-links {
            @include f('gp500');
            #{$parent}_header & {
                font-size: 12px;
                @include xs {
                    display: none; } }
            #{$parent}_footer & {
                font-size: 14px;
                @include sm {
                    display: none; }
                @include xs {
                    display: block;
                    margin-bottom: 15px; } } }
        &-icons {
            #{$parent}_header & {
                @include xs {
                    display: none; } }
            #{$parent}_footer & {} }

        &-link {
            display: inline-block;
            vertical-align: middle;
            text-shadow: 0 0 20px rgba(104, 193, 221, 0.2);
            color: $color5;
            line-height: 10px;
            transition: .3s ease-in-out;
            &:not(:last-child) {
                margin-right: 46px;
                @include sm {
                    margin-right: 6px; }
                @include xs {
                    margin-right: 15px; } }
            &:hover {
                transform: translateY(-5px); } }
        &-icon {
            display: inline-block;
            vertical-align: middle;
            transition: .3s ease-in-out;
            font-size: 12px;
            #{$parent}_footer & {
                &:not(:last-child) {
                    margin-right: 25px; } }
            &:hover {
                transform: translateY(-5px); }
            svg {
                fill: $color5; } } }
    &__btn {
        flex: 0 1 210px;
        max-width: 210px;
        color: #fff;
        font-size: 14px;
        @include sm {
            max-width: none; }
        @include xs {
            flex: 0 1 140px;
            padding: 12px 15px 10px; } } }






