@import "../helpers/all";

.header {
    $parent: &;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    transition: .3s ease-in-out;
    &.is-fixed {
        transform: translateY(-70px); }
    &__contact {
        padding: 15px 0;
        &-container {} }
    &__line {
        padding: 20px 0px;
        box-shadow: 0 15px 20px rgba(18, 3, 58, 0.07);
        background-color: #7352c5;
        transition: .3s ease-in-out;
        @include xs {
            padding: 10px 0; }
        .is-fixed  & {
            box-shadow: 0 15px 15px rgba(18, 3, 58, 0.15); }

        &-container {
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include sm {
                align-items: flex-end; }
            @include xs {
                align-items: center; } } }
    &__logo {
        display: flex;
        align-items: center;
        flex: 0 0 172px;
        margin-right: 100px;
        @include sm {
            margin-right: 40px; }
        &-title {
            margin-left: 14px;
            @include f('ex700',30,30);
            color: #fff;
            @include xs {
                font-size: 12px;
                line-height: 22px; } } }
    &__nav {
        flex: 0 1 848px;
        @include xs {
            flex-direction: columns;
            justify-content: center;
            position: absolute;
            top: 130px;
            left: 0;
            width: 100%;
            height: 100vh;
            z-index: 10;
            padding-bottom: 130px;
            overflow: auto;
            opacity: 0;
            border: 0;
            visibility: hidden;
            transform: translate .4s ease, opacity .4s ease , visibility 0s .4s , -webkit-transform .4s ease;
            background: #fff;
            text-align: center;
            box-shadow: inset 0 0 10px rgba($color1, .5); }
        &.is-active {
            opacity: 1;
            visibility: visible;
            transform: translate3d(0, 0, 0);
            transition: transform .4s ease, opacity .4s ease , visibility 0s 0s, -webkit-transform .4s ease; }
        &-list {
            display: flex;
            justify-content: space-between;
            margin-right: 14px;
            @include sm {
                margin-right: 0px;
                padding-bottom: 4px; }
            @include xs {
                flex-direction: column;
                justify-content: center;
                align-items: center;
                overflow: auto;
                height: 100%;
                padding-bottom: 0px; }
            li {
                &:not(:last-child) {
                    @include xs {
                        margin-bottom: 30px; } }
                a {
                    display: block;
                    position: relative;
                    font-size: 16px;
                    line-height: 1;
                    @include sm {
                        font-size: 12px; }
                    @include xs {
                        @include f('gp500');
                        font-size: 16px; }
                    &:hover {
                        span:nth-child(1) {
                            opacity: 0;
                            @include xs {
                                opacity: 1; } }
                        span:nth-child(2) {
                            opacity: 1;
                            transform: translate3d(-50%,-15px,0);
                            color: $white; } }
                    span {
                        color: $color3;
                        transition: .3s ease-in-out;
                        white-space: nowrap;
                        @include xs {
                            color: $color6; }
                        &:nth-child(2) {
                            opacity: 0;
                            @include vcenter;
                            z-index: 1;
                            @include f('gp500');
                            @include xs {
                                display: none; } } } } } } }
    &__nav-btn {
        display: none;
        $parent: &;
        position: relative;
        top: -5px;
        border: 0;
        outline: 0;
        padding: 0;
        z-index: 11;
        @include xs {
            display: block; }
        &.is-active span {
            color: $color1; }
        &-burger {
            display: block;
            margin: auto;
            width: 40px;
            height: 40px;
            cursor: pointer; }
        &-lines {
            display: block;
            width: 40px;
            height: 3px;
            &:after, &:before {
                width: 40px;
                height: 3px; }
            position: relative;
            transform: translateY(25px);
            background: #fff;
            transition: all .3s;
            #{$parent}.is-active & {
                background: rgba(255, 255,255,0); }
            &:before {
                content: '';
                position: absolute;
                left: 0;
                bottom: 10px;
                background: #fff;
                transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
            &:after {
                content: '';
                position: absolute;
                left: 0;
                top: 10px;
                background: #fff;
                transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
            #{$parent}.is-active & {
                &:after {
                    top: 0;
                    transform: rotate(45deg);
                    transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
                &:before {
                    bottom: 0;
                    transform: rotate(-45deg);
                    transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); } } } } }










