body {
   position: relative;
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   @include f("gp400", 18, 28);
   color: $color1;
   @include xs {
      font-size: 16px;
      line-height: 26px; }
   &.is-locked {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 0;
      overflow: hidden; } }
.container {
   max-width: 1170px;
   margin: 0 auto;
   padding: 0 15px; }
.row {
   display: flex;
   flex-wrap: wrap;
   margin: 0 -15px; }
.main {
   overflow: hidden; }
.section-title {
   $parent: &;
   position: relative;
   padding-bottom: 20px;
   @include xs {
      padding-bottom: 15px; }
   &_left {
      text-align: left;
      &::after {
         left: 0; } }
   &_center {
      text-align: center;
      &::after {
         left: 50%;
         margin-left: -25px; } }
   &::after {
      position: absolute;
      bottom: 0;
      width: 50px;
      height: 2px;
      background-color: $color5;
      content: ""; }

   &__label {
      @include f('gp700',12,12);
      letter-spacing: 2.4px;
      color: $color3;
      text-transform: uppercase; }
   &__text {
      @include f('ex700',34,36);
      color: $color4;
      @include xs {
         font-size: 30px;
         line-height: 32px; }
      #{$parent}_white & {
         color: #fff; }
      #{$parent}_md & {
         font-size: 36px;
         @include xs {
            font-size: 30px; } } }
   &__icon {
      position: absolute;
      top: 27px;
      z-index: -1;
      transform: rotate(-7deg);
      margin-left: -40px;
      width: 40px;
      height: 40px;
      fill: rgba(#d2c8ec,.3);
      #{$parent}_center & {
         left: 50%; }
      #{$parent}_left & {
         left: 30px; } } }

.btn {
   position: relative;
   display: inline-block;
   padding: 14px 50px 13px;
   outline: 0;
   border-radius: 50px;
   transition: .3s ease-in-out;
   @include f('gp400',16);
   line-height: 1.2;
   color: #fff;
   &::after {
      @include coverdiv;
      transform: translate(8px,5px);
      border-radius: 50px;
      transition: .3s ease-in-out;
      pointer-events: none;
      content: " "; }
   &::hover {
      background-color: $color6;
      &::after {
         transform: translate(0); } }
   &__icon {
      &-circle {
         position: absolute;
         top: 50%;
         right: 20px;
         display: flex;
         justify-content: center;
         align-items: center;
         width: 22px;
         height: 22px;
         margin-top: -11px;
         background-color: rgba($white, .3);
         border-radius: 50%; } }
   &_type-1 {
      padding: 12px 44px 10px;
      box-shadow: 0 15px 40px rgba(18, 3, 58, 0.15);
      border: 1px solid #fff;
      &::after {
         content: '';
         border: 1px solid rgba($white, .15); }
      &:hover {
         border-color: rgba($white,.6);
         &::after {
            border-color: transparent; } } }
   &_type-3 {
      background-color: $color6;
      &:hover {
         background-color: darken($color6, 6); }
      &::after {
         border: 1px solid rgba(#5f3cb7, .15); } }

   &_type-4 {
      position: relative;
      padding: 16px 61px 15px 33px;
      box-shadow:  0 15px 40px rgba(18, 3, 58, 0.15), 0 0 20px rgba(104, 193 ,221, 0.2);
      background-color: $color5;
      @include f('gp700',16);
      &:hover {
         background-color: darken($color5,15); }
      &::after {
         display: none; }

      svg {
         width: 8px;
         height: 8px;
         fill: $white; } } }
.circled-list {
   li {
      &::before {
         display: inline-block;
         vertical-align: middle;
         background-color: currentColor;
         width: 5px;
         height: 5px;
         margin-right: 5px;
         border-radius: 50%;
         content: ""; } } }
p {
   a {
      color: $color5;
      border-bottom: 1px solid rgba($color5, .6);
      transition: .3s easy-in-out;
      &:hover {
         border-bottom-color: transparent; } } }
strong {
   @include f("gp700"); }
.box-block {
   box-shadow: 0 15px 40px rgba(18, 3,58,0.15);
   border-radius: 4px;
   background-color: #fff;
   font-size: 16px;
   line-height: 24px;
   transition: .3s  ease-in-out;
   &:hover {
      transform: translateY(-10px); } }








.decor-img {
   position: absolute;
   z-index: -1; }



